/* Contact.css */

.contact-container {
    width: auto;
    flex-direction: column;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    align-content: center; 
 
    position: absolute;
    flex-direction: column;
}

.contact-info {
    margin-top: 20px;
}

.social-links {
    margin-top: 20px;

    a {
        display: inline-block;
        margin: 0 10px;
    }

    img {
        width: 30px;
        height: 30px;
        border-radius: 50%;
        transition: transform 0.3s ease-in-out;
    }

    img:hover {
        transform: scale(1.2);
    }
}

.copyright {
    margin-top: 20px;
    font-size: 14px;
    color: #ffffff;
}