.img_container {
    max-width: 600px;
    margin: 50px auto;
    padding: 20px;
    border: 1px solid #ddd;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    text-align: center;
}

.title_img {
    color: white;
}

.input {
    display: none;
    /* Oculta el input de tipo archivo */
}
.input-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 20px 0;
  }
label {
    display: block;
    margin: 20px 0;
    padding: 10px 15px;
    background-color: #3498db;
    color: #fff;
    border-radius: 5px;
    cursor: pointer;
}

.button {
    background-color: #2ecc71;
    color: #fff;
    padding: 10px 15px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
    margin-top: 10px;
}

progress {
    width: 100%;
    margin-top: 10px;
}

.p {
    color: #e74c3c;
    margin-top: 10px;
}